@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.91322885.ttf");
}

html {
  --moyo-color: #00acdc;
  text-align: left;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
}

.ul {
  flex-flow: row;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.link {
  cursor: pointer;
  color: #000;
  text-transform: uppercase;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.link-logo {
  align-items: center;
  display: flex;
}

.nav-list {
  margin-right: 20px;
}

.ul > :last-of-type:last-of-type {
  margin: 0;
}

.link:hover {
  color: var(--moyo-color);
}

.is-active {
  color: var(--moyo-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--moyo-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
